.info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.tab {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 16px;
  padding: 1rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border-bottom: 0.2em solid transparent;
}

.tab[disabled] {
  cursor: default;
}

.tab h1, .tab h2 {
  margin: 0;
}

.tab h2 {
  font-weight: normal;
  font-size: 16px;
  font-size: 1rem;
}

.tab h1 {
  font-weight: normal;
  font-size: 43.2px;
  font-size: 2.7rem;
}

.tab h1 small {
  color: #DDDDDD;
  font-size: 22.4px;
  font-size: 1.4rem;
}

.tab h1 span {
  font-size: 16px;
  font-size: 1rem;
}

.selected {
  composes: tab;
  border-bottom-color: white;
}



