.header {
  background-color: #aa49ff;
  color: white;
}

.content {
  padding: 16px;
  padding: 1rem;
}

.header h1 {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: normal;
  padding: 16px;
  padding: 1rem;
  margin: 0;
}

.tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.tabs a {
  display: block;
  text-align: center;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 16px 0;
  padding: 1rem 0;
  text-decoration: none;
  color: #DDDDDD;
  border-bottom: 0.2em solid transparent;
  text-transform: uppercase;
  font-size: 14.4px;
  font-size: 0.9rem;
}

.tabs a.active {
  color: white;
  border-bottom-color: white;
}



